/*
 * Article Page
 * Specific styles for this page 
 */
#news-article-container {
  row-gap: 2rem;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
}
@media screen and (min-width: 600px) {
  #news-article-container {
    column-gap: 2rem;
  }
}
@media screen and (min-width: 768px) {
  #news-article-container {
    width: 90%;
  }
}
#news-article-container > article {
  grid-column: 1/span 12;
}
@media screen and (min-width: 992px) {
  #news-article-container > article {
    grid-column: 1/span 9;
  }
}
#news-article-container > article #news-article-content {
  padding: 0 0 2rem;
}
#news-article-container > article #news-article-content p:not(:last-child) {
  margin-bottom: 1.5em;
}
#news-article-container > article #news-article-content-header {
  margin-bottom: 1.5em;
  padding-bottom: 1em;
  border-bottom: 1px solid lightgray;
}
#news-article-container > article #news-article-content-header h1 {
  text-align: center;
}
@media screen and (min-width: 600px) {
  #news-article-container > article #news-article-content-header h1 {
    text-align: initial;
  }
}
#news-article-container > article #news-article-content-header > div:not(:last-child) {
  margin-bottom: 1em;
}
#news-article-container > article #news-article-content-header > div:first-of-type {
  display: flex;
  flex-direction: column;
  row-gap: 0.5rem;
}
@media screen and (min-width: 600px) {
  #news-article-container > article #news-article-content-header > div:first-of-type {
    flex-direction: initial;
    align-items: center;
    column-gap: 1rem;
  }
}
#news-article-container > article #news-article-content-header > div:first-of-type span:not(:first-child) {
  padding-left: 3.7rem;
}
@media screen and (min-width: 600px) {
  #news-article-container > article #news-article-content-header > div:first-of-type span:not(:first-child) {
    padding: initial;
  }
}
#news-article-container > article #news-article-content-header > div:first-of-type span:nth-child(2) {
  margin-top: -0.85rem;
}
@media screen and (min-width: 600px) {
  #news-article-container > article #news-article-content-header > div:first-of-type span:nth-child(2) {
    margin: initial;
  }
}
#news-article-container > article #news-article-content-header > div:first-of-type span i {
  margin-right: 0.8rem;
}
#news-article-container > article #news-article-content-header > div:first-of-type img {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  margin-right: 0.8em;
}
#news-article-container > article #news-article-content-header > div:last-of-type {
  display: flex;
  column-gap: 0.5em;
}
@media screen and (max-width: 600px) {
  #news-article-container > article #news-article-content-header > div:last-of-type {
    align-items: center;
    justify-content: center;
  }
}
#news-article-container > article #news-article-content-header > div:last-of-type .tag {
  font-size: 0.8rem;
  padding: 0.5rem 1rem;
  display: inline-block;
  border: 1px solid steelblue;
}
#news-article-container > article #news-article-image {
  height: 30em;
  margin-bottom: 2.5rem;
  background-color: rgba(0, 0, 0, 0.05);
}
@media screen and (max-width: 375px) {
  #news-article-container > article #news-article-image {
    height: auto;
  }
}
#news-article-container > article #news-article-image img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
#news-article-container > article #news-article-share {
  display: flex;
  color: white;
  font-size: 1.2em;
  padding: 1rem 3rem;
  justify-content: space-between;
  grid-template-columns: repeat(12, 1fr);
  background-color: #1e1e1e;
}
#news-article-container > article #news-article-share div {
  display: flex;
  align-items: center;
}
#news-article-container > article #news-article-share div:last-child {
  justify-content: flex-end;
}
#news-article-container > article #news-article-share div:last-child a:not(:last-child) {
  margin-right: 0.5em;
}
#news-article-container > aside {
  grid-column: 1/span 12;
}
@media screen and (min-width: 992px) {
  #news-article-container > aside {
    grid-column: 10/span 3;
    padding-left: 2rem;
    border-left: 1px solid #ced4da;
  }
}
#news-article-container > aside > section:not(:last-child) {
  margin-bottom: 2rem;
}
#news-article-container > aside > section h3 {
  margin-bottom: 1rem;
}
#news-article-container > aside > section#categories ul li {
  padding: 0.5rem 0.8rem;
  display: inline-block;
  border: 1px solid steelblue;
  margin-bottom: 0.5rem;
}
#news-article-container > aside > section#categories ul li:hover {
  color: white;
  cursor: pointer;
  background-color: steelblue;
}
#news-article-container > aside > section#categories ul li:not(:last-child) {
  margin-right: 0.5em;
}
#news-article-container > aside > section#articles ul li:not(:last-child) {
  margin-bottom: 1em;
}
#news-article-container > aside > section#articles ul li img {
  width: 100%;
  object-fit: cover;
  max-height: 12rem;
}
#news-article-container > aside > section#articles ul li div {
  padding: 1rem 1.5rem;
  background-color: #e6e6e6;
}
@media screen and (min-width: 992px) {
  #news-article-container > aside > section#articles ul li div {
    font-size: 0.9rem;
  }
}