/*
 * Article Page
 * Specific styles for this page 
 */

@use "../../abstracts/" as *;

#news-article-container {
  row-gap: 2rem;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(12, 1fr);

  @include min-responsify($device-small) {
    column-gap: 2rem;
  }

  @include min-responsify($device-medium) {
    width: 90%;
  }

  > article {
    grid-column: 1 / span 12;

    @include min-responsify($device-large) {
      grid-column: 1 / span 9;
    }

    #news-article {
      &-content {
        padding: 0 0 2rem;

        p {
          &:not(:last-child) {
            margin-bottom: 1.5em;
          }
        }

        &-header {
          margin-bottom: 1.5em;
          padding-bottom: 1em;
          border-bottom: 1px solid lightgray;

          h1 {
            text-align: center;

            @include min-responsify($device-small) {
              text-align: initial;
            }
          }

          > div {
            &:not(:last-child) {
              margin-bottom: 1em;
            }

            &:first-of-type {
              display: flex;
              flex-direction: column;
              row-gap: 0.5rem;

              @include min-responsify($device-small) {
                flex-direction: initial;
                align-items: center;
                column-gap: 1rem;
              }

              span {
                &:not(:first-child) {
                  padding-left: 3.7rem;

                  @include min-responsify($device-small) {
                    padding: initial;
                  }
                }

                &:nth-child(2) {
                  margin-top: -0.85rem;

                  @include min-responsify($device-small) {
                    margin: initial;
                  }
                }

                i {
                  margin-right: 0.8rem;
                }
              }

              img {
                width: 3rem;
                height: 3rem;
                border-radius: 50%;
                margin-right: 0.8em;
              }
            }

            &:last-of-type {
              display: flex;
              column-gap: 0.5em;

              @include max-responsify($device-small) {
                align-items: center;
                justify-content: center;
              }

              .tag {
                font-size: 0.8rem;
                padding: 0.5rem 1rem;
                display: inline-block;
                border: 1px solid $color-primary-theme;
              }
            }
          }
        }
      }

      &-image {
        height: 30em;
        margin-bottom: 2.5rem;
        background-color: rgb(0, 0, 0, 0.05);

        @include max-responsify($device-mobile) {
          height: auto;
        }

        img {
          width: 100%;
          height: 100%;
          object-fit: contain;
        }
      }

      &-share {
        display: flex;
        color: white;
        font-size: 1.2em;
        padding: 1rem 3rem;
        justify-content: space-between;
        grid-template-columns: repeat(12, 1fr);
        background-color: $color-footer;

        div {
          display: flex;
          align-items: center;

          &:last-child {
            justify-content: flex-end;

            a:not(:last-child) {
              margin-right: 0.5em;
            }
          }
        }
      }
    }
  }

  > aside {
    grid-column: 1 / span 12;

    @include min-responsify($device-large) {
      grid-column: 10 / span 3;
      padding-left: 2rem;
      border-left: 1px solid $color-control-border;
    }

    > section {
      &:not(:last-child) {
        margin-bottom: 2rem;
      }

      h3 {
        margin-bottom: 1rem;
      }

      &#categories {
        ul li {
          padding: 0.5rem 0.8rem;
          display: inline-block;
          border: 1px solid $color-primary-theme;
          margin-bottom: 0.5rem;

          &:hover {
            color: white;
            cursor: pointer;
            background-color: $color-primary-theme;
          }

          &:not(:last-child) {
            margin-right: 0.5em;
          }
        }
      }

      &#articles {
        ul li {
          &:not(:last-child) {
            margin-bottom: 1em;
          }

          img {
            width: 100%;
            object-fit: cover;
            max-height: 12rem;
          }

          div {
            padding: 1rem 1.5rem;
            background-color: rgb(230, 230, 230);

            @include min-responsify($device-large) {
              font-size: 0.9rem;
            }
          }
        }
      }
    }
  }
}
